/* stylelint-disable selector-class-pattern */

.mapboxgl-popup-content {
  padding: 0;
  background-color: #151124;
}

.mapboxgl-popup-anchor-bottom-left .mapboxgl-popup-tip {
  border-top-color: #151124;
}

.popupContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 223px;
  height: 138px;
  color: #90909a;
  background-color: #151124;
  border-radius: 4px;
}

.popupContainer2 {
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 179px;
  height: 114px;
}

.popupDateLine {
  display: flex;
  justify-content: space-between;
  height: 18px;
}

.popupBlock {
  display: flex;
  align-items: center;
}

.popupIcon {
  width: 24px;
  height: 24px;
}

.popupValue {
  margin-left: 8px;
}

.popupCords {
  display: flex;
  align-items: flex-start;
  height: 32px;
}

.popupCordsValues {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: -3px;
  margin-left: 6px;
}
