/* stylelint-disable font-family-name-quotes */

/* inter-100 - latin */
@font-face {
  font-weight: 100;
  font-family: 'Inter';
  font-style: normal;
  src: url('../fonts/inter-v11-latin-100.eot'); /* IE9 Compat Modes */
  src:
    local(''),
    url('../fonts/inter-v11-latin-100.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */ url('../fonts/inter-v11-latin-100.woff2') format('woff2'),
    /* Super Modern Browsers */ url('../fonts/inter-v11-latin-100.woff') format('woff'),
    /* Modern Browsers */ url('../fonts/inter-v11-latin-100.ttf') format('truetype'),
    /* Safari, Android, iOS */ url('../fonts/inter-v11-latin-100.svg#Inter') format('svg'); /* Legacy iOS */
}

